
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 20px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">验票</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;margin-top: 20px">
            <el-row>
                <el-col class="el-col"  style="margin-left: 0;width: 500px">
                    <p class="input-title" style="font-size: 20px !important;">票码核验:</p>
                    <el-input :autofocus="autofocus" class="big" v-model="scanurl" @input="ticketInput" @keyup.enter.native="checkUp"   :placeholder="placeholder">
                    </el-input>
                </el-col>

                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <p style="height: 15px"></p>
                    <el-button type="primary"  id="submit"  @click = "checkUp">核   验</el-button>
                </el-col>

            </el-row>
        </el-col>
        <div style="margin-left: 100px" class="infos" v-if="showinfo==1">
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block">证件类型:</span> <span>{{inspectData.certiTypeText}}</span></p>
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">证件号码:</span> <span>{{inspectData.certiNum}}</span></p>
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">姓名:</span> <span>{{inspectData.name}}</span></p>
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">出发地:</span> <span>{{inspectData.depStation}}</span></p>
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">到达地:</span> <span>{{inspectData.destStation}}</span></p>
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">验票码:</span> <span>{{inspectData.ticketNum}}</span></p>
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">验票时间:</span> <span>{{inspectData.ticketTime}}</span></p>
            <p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">核验状态:</span> <span style="color: red">{{inspectData.validateStatusText}}</span></p>
        </div>
        <div style="margin-left: 100px" class="infos" v-if="showinfo==2">
            <p style="font-size: 22px;color: red">{{inspectData}}</p>
        </div>
        <!--<el-dialog-->
            <!--title="核验成功"-->
            <!--:visible.sync="dialogVisible"-->
            <!--width="30%"-->
           <!--&gt;-->
            <!--<p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block">证件类型:</span> <span>{{inspectData.certiTypeText}}</span></p>-->
            <!--<p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">证件号码:</span> <span>{{inspectData.certiNum}}</span></p>-->
            <!--<p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">姓名:</span> <span>{{inspectData.name}}</span></p>-->
            <!--<p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">出发地/到达地:</span> <span>{{inspectData.depStation}}/{{inspectData.destStation}}</span></p>-->
            <!--<p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">验票码:</span> <span>{{inspectData.ticketNum}}</span></p>-->
            <!--<p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">验票时间:</span> <span>{{inspectData.ticketTime}}</span></p>-->
            <!--<p><span style="font-weight: 600;margin-right: 20px;width: 100px;text-align: right;display: inline-block ">核验状态:</span> <span>{{inspectData.validateStatusText}}</span></p>-->
            <!--<span slot="footer" class="dialog-footer">-->

                <!--<el-button type="primary" @click="inspectSure">确 定</el-button>-->
            <!--</span>-->
        <!--</el-dialog>-->
    </div>
</template>
<script>
    import ajax from "../model/sys/index"


    export default {
        data() {
            return {

                formLabelWidth: '120px',
                dialogVisible:false,
                scanurl:"",
                serialId:"",
                ticketNum:"",
                placeholder:"请输入票码",
                token:"",
                inspectData:{},
                showinfo:0,
                autofocus:true
            };
        },
        methods: {

            GetQueryString(param,url) { //param为要获取的参数名 注:获取不到是为null
                var currentUrl = url; //获取当前链接
                var arr = currentUrl.split("?");//分割域名和参数界限
                if (arr.length > 1) {
                    arr = arr[1].split("&");//分割参数
                    for (var i = 0; i < arr.length; i++) {
                        var tem = arr[i].split("="); //分割参数名和参数内容
                        if (tem[0] == param) {
                            return tem[1];
                        }
                    }
                    return null;
                }
                else {
                    return null;
                }
            },

            ticketInput(){


            },
            inspectSure(){
                this.dialogVisible=false
                this.scanurl=""
                this.ticketNum = ""
                this.serialId = ""
            },
            checkUp(){
                if(this.scanurl.indexOf("code")!=-1){
                    var thisUrl = this.scanurl
                    this.scanurl = this.GetQueryString('ticketNum',thisUrl)
                    this.ticketNum = this.GetQueryString('ticketNum',thisUrl)
                    this.serialId = this.GetQueryString('serialId',thisUrl)
                    this.token = localStorage.getItem("token")
                }else {
                    this.ticketNum = this.scanurl
                    this.token = localStorage.getItem("token")
                    this.serialId=""
                }
                var params = {
                    st:this.token,
                    serialId: this.serialId,
                    ticketNum:this.ticketNum,
                    token:this.token
                }
                for(var i in params){
                    if(String(params[i])==""){
                        delete params[i]
                    }
                }
                ajax.orderValidate(params,res=>{

                    if(res.data.code=="200"){
                        this.showinfo=1
                        this.inspectData = res.data.result
                        this.scanurl=""
                        this.ticketNum = ""
                        this.serialId = ""

                    }else if(res.data.code=="703"){
                        this.showinfo=2
                        this.inspectData = res.data.message
                        this.scanurl=""
                        this.ticketNum = ""
                        this.serialId = ""

                    }else if(res.data.code=="500") {
                        this.scanurl=""
                        this.ticketNum = ""
                        this.serialId = ""
                    }
                })
            },
        },
        mounted(){
            this.autofocus=true
        }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .big .el-input__inner{
        height: 60px;
        font-size: 22px;
    }
    .infos p{
        margin-bottom: 10px ;
        font-size: 20px;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



